import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollDerazonaRonGroupService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Ron_group } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollDerazonaRonGroupService: PayrollDerazonaRonGroupService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_ron_group: string = ''

    ngOnInit() {
        this.id_ron_group = this.activatedRoute.snapshot.paramMap.get('id_ron_group')
        this.form();
        this.getData();
    }

    getData() {
        this.payrollDerazonaRonGroupService.getPayrollDerazonaRonGroupsId(this.id_ron_group).subscribe(
            data=> {
                this.komponen.setValue(data.data)
            }
        )
    }
    komponen: FormGroupTypeSafe<Ron_group>;
    form() {
        this.komponen = this.fb.group<Ron_group>(
            {
                name_group: new FormControl('', [Validators.required]),
                status: new FormControl(''),
                id_ron_group: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.payrollDerazonaRonGroupService.putPayrollDerazonaRonGroupsId({id: this.id_ron_group, body: this.komponen.value}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/ron-group/list'])
    }

}
