import { Component, OnInit } from '@angular/core';
import { PayrollDerazonaRonGroupService } from 'src/app/sdk/payroll/services';
import { Ron_group } from 'src/app/sdk/payroll/models';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollDerazonaRonGroupService: PayrollDerazonaRonGroupService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }


    ngOnInit() {
        this.form();
    }

    komponen: FormGroupTypeSafe<Ron_group>;
    form() {
        this.komponen = this.fb.group<Ron_group>(
            {
                name_group: new FormControl('', [Validators.required]),
                status: new FormControl(''),
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.payrollDerazonaRonGroupService.postPayrollDerazonaRonGroups(this.komponen.value).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/ron-group/list'])
    }

}
